import React, { Component } from 'react'




export class Contact extends Component {




    render() {
        return (
            <div className="min-vh-100 d-flex justify-content-center align-content-center align-items-center">
                <div className="row">
                    <div className="col-12 my-5">
                        <div className="d-flex justify-content-center flex-wrap">
                            <div className="col-12">
                                <h5 className="text-center mt-2">Questions?</h5>
                                <p className="text-center mb-5">Are you interested in a website, webapp or maybe a logo design? I might be the person you need!</p>
                                
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12">
                                <div className="d-flex justify-content-center flex-wrap">
                                    <form method="POST" action="https://formspree.io/alfanoangeloc@gmail.com">
                                        
                                        <div className="group">      
                                            <input type="text" required/>
                                            <span className="highlight"></span>
                                            <span className="bar"></span>
                                            <label>Your Name</label>
                                        </div>
                                        <div className="group">      
                                            <input type="email" required/>
                                            <span className="highlight"></span>
                                            <span className="bar"></span>
                                            <label>Email</label>
                                        </div>      
                                  
                                        <textarea  name="message" placeholder="Message" rows="1" ></textarea>
                                      
                                            <span className="bar"></span>
                                     
                                        <button  className="text-center mt-5" type="submit">Send</button>
                                    </form>
                                </div>
                            </div>
                        </div>
                  </div>
              </div>
                
            </div>
            
            
        )
    }
}

export default Contact